<template>
  <div
    class="modal fade"
    id="addemployee"
    data-backdrop="static"
    data-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl bfont">
      <div class="modal-content">
        <div class="row m-0 w-100 p-2">
          <div class="col-12 mt-3 bfont mainfont">เพิ่มพนักงาน</div>
          <div class="col-12 mt-3 mx-0 p-3">
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1">ชื่อพนักงาน</label>
              <input
                type="text"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.employees_name"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1">เต็มวัน</label>
              <input
                type="text"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.pay_full_time"
              />
            </div>
            <!-- --------------------------- -->

            <div class="form-group">
              <label for="exampleInputEmail1">ครึ่งวัน</label>
              <input
                type="text"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.pay_half_time"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1">หัก</label>
              <input
                type="text"
                class="form-control"
                aria-describedby="emailHelp"
                v-model="add.cut_late_for_work"
              />
            </div>
            <!-- --------------------------- -->
            <div class="form-group">
              <label for="exampleInputEmail1">ตำเเหน่ง</label>
              <select
                id="inputState"
                class="form-control"
                v-model="add.position_id"
              >
                <option value="">เลือกตำเเหน่ง...</option>
                <option
                  v-for="(e, i) in $store.getters['positionActions/GET_data']"
                  :key="i"
                  :value="e._id"
                >
                  {{ e.position_name }}
                </option>
              </select>
            </div>
            <!-- --------------------------- -->

            <div class="col-12 text-right">
              <button type="button" class="btn btn-danger" @click="close()">
                ปิด</button
              >&nbsp;&nbsp;<button
                type="button"
                class="btn btn-info"
                @click="adddata()"
              >
                ยืนยัน
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModalAddEmployee",
  data() {
    return {
      add: {
        employees_name: "",
        pay_full_time: "",
        pay_half_time: "",
        cut_late_for_work: "",
        position_id: "",
      },
    };
  },
  mounted() {
    this.redata();
    this.$store.dispatch("positionActions/getdata");
  },
  methods: {
    redata() {
      this.add = {
        employees_name: "",
        pay_full_time: "",
        pay_half_time: "",
        cut_late_for_work: "",
        position_id: "",
      };
    },
    async adddata() {
      try {
        var date = this.$dt(Date.now(), "yyyy-mm-dd");
        if (
          this.add.employees_name == "" ||
          this.add.pay_full_time == "" ||
          this.add.pay_half_time == "" ||
          this.add.cut_late_for_work == "" ||
          this.add.position_id == ""
        ) {
          throw "กรอกข้อมูลไม่ครบ";
        } else {
          let data = {
            employees_name: this.add.employees_name,
            pay_full_time: this.add.pay_full_time,
            pay_half_time: this.add.pay_half_time,
            cut_late_for_work: this.add.cut_late_for_work,
            position_id: this.add.position_id,
            
          };
          await this.$store.dispatch("employeeActions/add", data);
          this.close();
          this.$main.showSuccessAlert(this, "เรียบร้อย");
        }
      } catch (error) {
        alert(error);
      }
    },
    close() {
      this.$$("#addemployee").modal("hide");
    },
  },
};
</script>

