<template>
  <div class="row w-100 mx-0 mt-3 p-0">
    <div class="col-12">
      <div class="card">
        <div class="row w-100 m-0 p-2">
          <div class="col-12 text-right my-3">
            <button
              type="button"
              class="btn btn-info bfont"
              data-toggle="modal"
              @click="add()"
            >
              เพิ่ม
            </button>
          </div>
          <div class="col-12 my-3">
            <div class="card shadow rounded px-2 my-table">
              <table
                class="table my-3"
                v-if="$store.getters['employeeActions/GET_data'].length > 0"
              >
                <thead class="thead-bg">
                  <tr>
                    <th scope="col"></th>
                    <th scope="col">ชื่อพนักงาน</th>
                    <th scope="col">เต็มวัน</th>
                    <th scope="col">ครึ่งวัน</th>
                    <th scope="col">หัก</th>

                   
                    <th scope="col">Actions</th>
                  </tr>
                </thead>
                <!-- --------------------------- -->
                <tbody>
                  <tr
                    v-for="(e, i) in $store.getters['employeeActions/GET_data']"
                    :key="i"
                  >
                    <th scope="row">{{ i }}</th>
                    <td>{{ e.employees_name }}</td>
                    <td>{{ e.pay_full_time }}</td>
                    <td>{{ e.pay_half_time }}</td>
                    <td>{{ e.cut_late_for_work }}</td>
                     
                    <td>
                      <p>
                        <span
                          @click="
                            $router.push({
                              name: `EditEmployee`,
                              params: { id: e._id },
                            })
                          "
                          v-html="iconedit"
                        ></span
                        >&nbsp;&nbsp;<span
                          @click="remove(e._id)"
                          v-html="iconremove"
                        ></span>
                      </p>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <ModalAdd />
  </div>
</template>

<script>
import ModalAdd from "@/components/Employee/ModalAdd.vue";
export default {
  name: "Employee",
  components: { ModalAdd },
  data() {
    return {
      iconedit: `<svg xmlns="http://www.w3.org/2000/svg" 
      width="20" height="20" fill="#6495ED" class="bi bi-pencil-square" viewBox="0 0 16 16">
  <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
  <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
</svg>`,
      iconremove: `<svg xmlns="http://www.w3.org/2000/svg" 
      width="20" height="20" fill="#DC143C" class="bi bi-trash" viewBox="0 0 16 16">
  <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
  <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
</svg>`,
    };
  },
  mounted() {
    this.$store.dispatch("employeeActions/getdata");
  },
  methods: {
    add() {
      this.$$("#addemployee").modal("show");
    },
    remove(id) {
      if (confirm("คุณต้องการลบ หรือไม่?")) {
        try {
          this.$store.dispatch("employeeActions/remove", { id: id });
          this.$main.showSuccessAlert(this, "เรียบร้อย");
        } catch (error) {
          alert(error);
        }
      }
    },
  },
};
</script>

